.projectImage {
	height: 40px;
	width: 40px;
	margin: 0 10px;
}
.projectContainer {
	border-bottom: 1px solid rgba(0, 0, 0, 0.125);
	padding: 15px;
}

.projectTitle {
	line-height: 40px;
}

.projectIcon {
	line-height: 40px;
	color: #7a7b7b;
	margin-right: 20px;
}

.projectIcon:hover {
	cursor: pointer;
	color: #000;
}

.separator {
	margin: 0 10px;
	width: 10px;
}
