.listContainer {
	height: 80%;
	width: 96%;
	margin: 2%;
	border: 1px solid #e1e6ef;
	border-radius: 3px;
	background: #fff;
}

.listHeader {
	border-bottom: 1.5px solid rgba(26, 54, 126, 0.125);
	font-weight: 600;
}

.listHeaderTitle {
	padding: 15px;
}

.overflow-y-auto {
	overflow: hidden;
	overflow-y: auto;
}
.listAction {
	background-color: #37a000;
	color: #fff;
	padding: 15px;
}
