@font-face {
	font-family: 'Muli';
	src: local('Muli'), url(./fonts/Muli-VariableFont_wght.ttf) format('truetype');
}

body {
	margin: 0;
	font-family: 'Muli', sans-serif;
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
	background-color: #f1f3f6;
}

@font-face {
	font-family: 'icomoon';
	src: url('./fonts/icomoon.eot?wy8iyo');
	src: url('./fonts/icomoon.eot?wy8iyo#iefix') format('embedded-opentype'),
		url('./fonts/icomoon.ttf?wy8iyo') format('truetype'), url('./fonts/icomoon.woff?wy8iyo') format('woff'),
		url('./fonts/icomoon.svg?wy8iyo#icomoon') format('svg');
	font-weight: normal;
	font-style: normal;
	font-display: block;
}

[class^='icon-'],
[class*=' icon-'] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: 'icomoon' !important;
	speak: none;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;

	/* Enable Ligatures ================ */
	letter-spacing: 0;
	-webkit-font-feature-settings: 'liga';
	-moz-font-feature-settings: 'liga=1';
	-moz-font-feature-settings: 'liga';
	-ms-font-feature-settings: 'liga' 1;
	font-feature-settings: 'liga';
	-webkit-font-variant-ligatures: discretionary-ligatures;
	font-variant-ligatures: discretionary-ligatures;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

.icon-pencil:before {
	content: '\e905';
}
.icon-library:before {
	content: '\e921';
}
.icon-users:before {
	content: '\e972';
}
.icon-key:before {
	content: '\e98d';
}
.icon-bin:before {
	content: '\e9ac';
}
.icon-eye:before {
	content: '\e9ce';
}
.icon-plus:before {
	content: '\ea0a';
}
.icon-exit:before {
	content: '\ea14';
}
