.loginContainer {
	background: #fff;
	width: 400px;
	margin: 0 auto;
}

.login-title {
	padding: 15px;
	border-bottom: 1px solid #ccc;
	font-weight: 600;
}

.login-form {
	padding: 15px;
}

.form-control {
	margin: 5px 0px;
}
.form-input {
	width: 100%;
	height: 34px;
	padding: 6px 12px;
	line-height: 1.428;
	font-size: 14px;
	color: #555;
	border: 1px solid #ccc;
	box-shadow: inset 0 1 1 rgba(0, 0, 0, 0.075);
	transition: border-color ease-in-out 0.15s, box-shadow ease-in-out 0.15s;
}

.form-btn {
	margin-top: 10px;
	background: #37a000;
	display: inline-block;
	padding: 10px;
	color: #fff;
	font-size: 16px;
	font-weight: 700;
	border-radius: 3px;
	cursor: pointer;
}
.verrMsg {
	color: #ff0000;
	font-size: 14px;
	margin: 5px 0;
}
.pd15 {
	padding: 15px;
}
